import React from "react";
import PropTypes from "prop-types";

const SuperGeilesMenu = props => {
  const { to, mytitle } = props;

  return (
    <React.Fragment>
      <li key={mytitle} className="linormal"><a href={to}>{mytitle}</a></li>
      {/* --- STYLES --- */}
        <style jsx>{`
        .linormal{
        font-size: smaller;
        padding-top: 3px;
        }
        `}</style>
    </React.Fragment>
  );
};

SuperGeilesMenu.propTypes = {
  to: PropTypes.string,
  mytitle: PropTypes.string,
};

export default SuperGeilesMenu;
