import React from "react";
import PropTypes from "prop-types";
import SuperGeilesMenu from "../Article/SuperGeilesMenu";

import Headline from "../Article/Headline";
import Bodytext from "../Article/Bodytext";

const Page = props => {
  const {
    page: {
      html,
      frontmatter: { title }
    },
    theme
  } = props;

  let fixify = (html) => { 
      var template = document.createElement('template');
      template.innerHTML = html;
      var jumpmark=1;
      var tmp = template.content.querySelectorAll("h3,h4");
      for( var i=0; i<tmp.length; ++i){
        tmp[i].setAttribute("id", "jumpmark_" + jumpmark);
        jumpmark++;
      }
      return template.innerHTML;
  }
  let htmlfixed = fixify(html);
  let getdom = (html) => { 
      var template = document.createElement('template');
      template.innerHTML = html;
      return template.content.querySelectorAll("h3,h4");
  }

  let createLinks = () => {
    let table = []
    let xz = getdom(htmlfixed);

    let p = {}
    let opennew = false;
    let current = "";
    for (let z = 0; z < xz.length; z++) {
        if(xz[z].tagName == "H3"){
            opennew = true;
            current = xz[z].innerHTML;
            p[current]={}
        }
        if(xz[z].tagName == "H4" && current != ""){
            p[current][xz[z].innerHTML]=xz[z].getAttribute("id");
        }
    }
    console.log(p)

    for (var key in p) {
      //Inner loop to create children
      table.push(
        <React.Fragment>
        <li key={key}><p className="lititleinner">{key}</p></li>
         {/* --- STYLES --- */}
        <style jsx>{`
        .lititleinner{
        font-weight: bold;
        font-size: small;
        padding-top: 15px;
        padding-bottom:3px;
        }
        `}</style>
        </React.Fragment>)
      for (var lbl in p[key]) {
        let link="#" + p[key][lbl]
        let mylabel = lbl;
        table.push(<SuperGeilesMenu to={link} mytitle={mylabel} />)
      }

    }

    return table
  }

  return (
    <React.Fragment>
      <header>
        <Headline title={title} theme={theme} />
      </header>
      <div className="wrapper">
      <div className="main">
         <Bodytext html={htmlfixed} theme={theme} />
      </div>
      <div className="sidebar">
        <ul>
        {createLinks()}
        </ul>
      </div>
    </div>
     {/* --- STYLES --- */}
      <style jsx>{`

@media only screen and (max-width: 1026px) {
    .sidebar {
      visibility: hidden;
    }
}
.wrapper {
  display: flex;
  justify-content: space-between;
  width: 800px;
}

.sidebar ul{
  list-style:none;
  position:relative;
  overflow: hidden;
}

.lititleinner{
  font-weight: bold;
  font-size: smaller;

}

.sidebar {
  background: #e8e8e8 !important;
  padding: 15px;
  background-color: #fff;
  position: -webkit-sticky;
  position: sticky;
  top: 10vh;
  font-size: smaller;
    display: table;

}

.main {
  width: 78%;
}

.sidebar {
  width: 20%;
}
`}</style>
    </React.Fragment>

  );
};

Page.propTypes = {
  page: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired
};

export default Page;
